.header {
    height: 70px;
    padding-inline:  20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 2px 2px 2px rgb(192, 192, 192);
}


.logo {
    text-transform: uppercase;
    
}

.big {
    font-size: 30px;
    font-weight: 650;
}

.nav  {
    width: 500px;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    font-size: 19px;
    font-weight: 75;
}

.nav div{
    padding: 10;
}

.nav div:hover {
    background: #000;
    color: #FFFFFF;
    transition: background 0.5s;
}

.login {
    border: solid 1px rgb(157, 157, 157);
    padding: 11px 35px;
    border-radius: 51px;
    font-size: 19px;
}

.login div {
    transition: all 0.5s ease;

}

.login div:hover{
    transform: scale(1.1);
    color: #fff;
    background-color: #27E09D;
    box-shadow: 0px 26px 56px 0px rgba(0, 0, 0, 0.10), 0px 102px 102px 0px rgba(0, 0, 0, 0.09), 0px 230px 138px 0px rgba(0, 0, 0, 0.05), 0px 410px 164px 0px rgba(0, 0, 0, 0.01), 0px 640px 179px 0px rgba(0, 0, 0, 0.00);
}